import React, {useState} from 'react'
import { withRouter } from 'react-router-dom';
import { BrowserRouter as Router, Redirect, Route, NavLink, Switch } from 'react-router-dom'
import logo from '../../img/logo.png'
import './Downmenu.css'
class Downmenu extends React.Component {

	constructor(props) {
	  super(props);
	  this.state = {
        pathname:this.props.location,
        barname:"",
        selectval:'none'
	  };
	}

    showMenu(){
        if(this.state.selectval==='none'){
            this.setState({
                selectval:'block'
            })
        }else if(this.state.selectval==='block'){
            this.setState({
                selectval:'none'
            })
        }
    }

	render() {
        return (
            <nav>
                <div className="navbar-div">
                    <img src={logo} alt="" ></img>
                    <button onClick={this.showMenu.bind(this)}>
                        <span></span>
                        <span></span>
                        <span></span>
                    </button>
                </div>

                <div className="menu" style={{display: this.state.selectval }}>
                    <ul>
                        <li><a href="/">首页</a></li>
                        <li><a href="/My">我的</a></li>
                        <li><a href="/Login">登录</a></li>
                    </ul>
                </div>
            </nav>
        )
    }
}
export default  withRouter(Downmenu);