import React from 'react'
import Topbar from '../../components/Topbar/Topbar';
import './Confession.css'
class Confession extends React.Component {

	constructor(props) {
	  super(props);
	  this.state = {
		data:[]
	  };
	}
    componentDidMount(){
        const url="https://www.prebeauty.work/confession";
        //const param={
        //
        //};
        fetch(url,{
            method:'GET',
            //body:JSON.stringify(param),
            headers:{
                'content-type': 'application/json'
            }
        }).then((res)=>{
            if(res.status===200){
                res.json().then( (data)=> {
                    this.setState({
                        data:data
                    })
                    console.log(this.state.data);
                })
            }else {
                alert("出现一个问题");
            }
        })
    }

	render() {
        console.log(this.state.data)
        return (
            <div>
                <Topbar name="桃花遇" />     
                {
                    this.state.data.map(con=>
                        <div className="oneCon" key={con.confessionid}>
                            <img className="userimg"></img>
                            <div className="username">蟹黄包</div>

                            <div className="content">{con.ccontent}</div>

                            <img className="conimg"></img>
                            <img className="conimg"></img>
                            <img className="conimg"></img>

                            <i className="iconfont icon-aixin"></i>
                            <i className="iconfont icon-w_aixin"></i>
                            <i className="iconfont icon-53pinglun-"></i>
                            <i className="iconfont icon-fenxiang"></i>
                        </div>
                    )
                }
                
            </div>
        )
    }
}

export default Confession