import React from 'react';
import { withRouter } from 'react-router-dom';
import './Topbar.css'
class Navbar extends React.Component {

	constructor(props) {
	  super(props);
	  this.state = {
		pathname:this.props.location,
        barname:"",
        selectval:'none',
        name:this.props.name
	  };
	}

    showMenu(){
        if(this.state.selectval==='none'){
            this.setState({
                selectval:'block'
            })
        }else if(this.state.selectval==='block'){
            this.setState({
                selectval:'none'
            })
        }
    }

    goBack(){
        window.history.back(-1);
    }

	render() {
		const {pathname} =this.props.location;
        return (
            <nav>
                <div className="navbar-div">
                    <i className="iconfont icon-fanhui" 
                        style={{color:'#95D2AA'}} 
                        onClick={this.goBack.bind(this)}
                    ></i>
                    <div className="barname">{this.state.name}</div>
                    <button onClick={this.showMenu.bind(this)}>
                        <span></span>
                        <span></span>
                        <span></span>
                    </button>
                </div>

                <div className="menu" style={{display: this.state.selectval }}>
                    <ul>
                        <li><a href="/">首页</a></li>
                        <li><a href="/My">我的</a></li>
                        <li><a href="/Login">登录</a></li>
                    </ul>
                </div>
            </nav>
        );
	}
}

export default withRouter(Navbar);